import React, {useState} from 'react';

import {
    Button,
    TextField,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function AddFieldDialog({active, setActive, customer_id}) {
    const [field, setField] = useState("");

    function onSubmitField(){
        fetch(process.env.REACT_APP_API+'fields/'+ customer_id +'?format=json',{
            method: 'POST',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "name": field
            }) 
        })
    };

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>Добавить месторождение</DialogTitle>
                <Form name="addfield" onSubmit={onSubmitField}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="add_field"
                            onChange={e => setField(e.target.value)} 
                            placeholder="Введите название месторождения"
                            />
                        </Form.Group>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}>Сохранить</Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default AddFieldDialog;